import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';

i18n
    .use(initReactI18next)
    .use(LanguageDetector)
    .init({
        fallbackLng: 'ru',
        resources: {
            en: {
                translation: {
                    navbar: {
                        search: "Search...",
                        profile: "Profile",
                        account: 'Personal account',
                        login: 'Log In',
                        logout: 'Log Out',
                    },
                    profile: {
                        welcome: 'Welcome, {{name}}',
                        email: 'Your email:',
                        name: 'Your name:',
                        creationDate: 'Registration date:',
                        rights: 'Your rights:',
                        cancel: 'Cancel',
                        save: 'Save',
                        close: 'Close',
                        edit: 'Edit',
                        input: 'Input new name',
                        inputError: 'Input at least 2 symbols'
                    }
                    
                }
            },
            ru: {
                translation: {
                    navbar: {
                        search: "...",
                        profile: "",
                        account: ' ',
                        login: '',
                        logout: '',
                    },
                    profile: {
                        welcome: ' , {{name}}',
                        email: ' :',
                        name: ' :',
                        creationDate: ' :',
                        rights: ' :',
                        cancel: '',
                        save: '',
                        close: '',
                        edit: '',
                        input: '  ',
                        inputError: '  2 '
                    }
                }
            }
        }
    })

export default i18n;